html {
  background-color: #2d3442;
}
.App {
  text-align: center;
}

.topIndex {
  background-color: #282c34;
  width: 80%;
  min-height: calc(10px + 2vmin);
  display: flex;
  align-items: left;
  justify-content:left;
  font-size: calc(10px + 2vmin);
  color: white;
  margin: 0 auto;
  flex-wrap: wrap;
  stroke-width: 0;
  word-wrap: break-word;
  white-space:initial;
}

.topTop {
  background-color: #282c34;
  width: 100%;
  min-height: calc(10px + 2vmin);
  display: flex;
  align-items: left;
  justify-content:left;
  font-size: calc(10px + 2vmin);
  color: white;
  margin: 0 auto;
  flex-wrap: wrap;
  stroke-width: 0;
}

.resultIndex {
  background-color: #282c34;
  width: 80%;
  /*min-height: calc(10px + 2vmin);*/
  display: flex;
  align-items: left;
  justify-content:left;
  font-size: calc(10px + 2vmin);
  color: white;
  margin: 0 auto;
  flex-wrap: wrap;
}

.TextButton {
  background-color: #282c34;
  display: inline-block;
  vertical-align: middle;
  padding: 0.5em;
  line-height: 1;
  border-radius: 4px;
  cursor: pointer;
  user-select: none;
}

.MarginDiv {
  min-height: calc(10px + 2vmin);
  width: 100%;
}

.Submit {
  background-color: #282c34;
  color: white;
  border: none;
  border-radius: auto;
  cursor: pointer;
  min-height: calc(10px + 2vmin);
  margin-top: calc(5px + 1vmin);
}